<template>

  <div v-bind:class="backClass+backIndex">
    <div class="login">
      <div class="welcome">{{welcome}}</div>
      <div class="header free-lianghao-entry" data-bind="visible: isChs()"><a href="javascript:void(0)" data-bind="click: toLianghao">{{checkmsg}}</a></div>
      <div class="gt1">{{gt}}</div>
      <div class="header">{{gt2}}</div>
      <form>
        <div>
          <input type="text" placeholder="昵称" class="common-input" /><br />
        </div>
        <div>
          <input type="password" placeholder="密码" class="common-input" />
        </div>
        <div class="forms">
          <select style="margin: 0px 19px 0px 0px;" class="common-input area">
            <option value="">+86 &nbsp;&nbsp;中国</option>
            <option value="">+852 中国香港特别行政区</option>
            <option value="">+853 中国澳门特别行政区</option>
            <option value="">+886 中国台湾地区</option>
          </select>
          <input type="text" id="phone" placeholder="手机号码" class="common-input" />
        </div>
        <label class="input-tips">可通过该手机号找回密码</label>
        <div>
          <button>立即注册</button>
        </div>

        <span class="xieyi">
          <input class="c1" type="checkbox"><label class="c2">我已阅读并同意相关服务条款和隐私政策</label>
        </span>
      </form>
    </div>
    <div class="img">
      <ul>
        <li><img src="@/assets/IMG_2464.png" width="300" height="300" /></li>
        <li><img src="@/assets/IMG_2465.png" width="300" height="300" /></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      welcome: '欢迎注册',
      gt: '飞天连雪射白鹿，',
      gt2:'笑书神侠倚碧鸳。',
      checkmsg:'领取资格',
      backClass: 'hello',
      backIndex: 0
    }
  },
  mounted() {
    this.change()
  },
  methods: {
    change() {
      console.log(this.backIndex)
      setInterval(() => {
        console.log(this.backIndex)
        if (this.backIndex === 3) {
          this.backIndex = 0
        }
        this.backIndex += 1
        console.log(this.backIndex)
      }, 6000)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello0 {
  background: url('../assets/back.jpg') no-repeat center;
  background-size: 100% 100%;
}
.hello1 {
  background: url('../assets/back1.jpg') no-repeat center;
  background-size: 100% 100%;
}
.hello2 {
   background: url('../assets/back2.jpg') no-repeat center;
  background-size: 100% 100%;
}
.hello3 {
  background: url('../assets/back3.jpg') no-repeat center;
  background-size: 100% 100%;
}
.login {
  position: relative;
  margin: 0 auto;
  width: 480px;
}
.welcome {
  font-size: 44px;
  margin-bottom: 20px;
}
.gt1{
  font-size: 28px;
  line-height: 1.2;
  color: #333;
}
.header {
  font-size: 28px;
  margin-bottom: 44px;
  line-height: 1.2;
  color: #333;
}
.free-lianghao-entry {
  float: right;
}
a {
  outline: 0;
  text-decoration: none;
  color: #359eff;
  font-size: 24px;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.input-tips {
  font-size: 14px;
  height: 14px;
  color: #999;
  white-space: nowrap;
  bottom: 0;
}
.forms {
  display: flex;
  flex-direction: row;
  align-items: center;
}
div {
  padding-top: 28px;
}
.c2,
.c1 {
  line-height: 30px;
  font-size: 13px;
  color: #aaa;
}
.c1 {
  vertical-align: text-bottom;
  margin-bottom: 2px;
}
.common-input,
select {
  border: 1px #aaa solid;
  border-radius: 4px;
  background: 0 0;
  text-align: left;
  font-size: 20px;
  width: 408px;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
}

.area {
  width: 112px;
}
#phone {
  width: 277px;
}
* {
  margin: 0;
  padding: 0;
  border: none;
  transition: all 0.2s;
}
button {
  border: 1px #3083ff solid;
  border-radius: 4px;
  background-color: #3487ff;
  box-shadow: 0 5px 8px 0 rgba(24, 95, 255, 0.1);
  color: #fff;
  text-align: center;
  font-weight: lighter;
  background-image: linear-gradient(0deg, #398bff, #3083ff);
  width: 448px;
  height: 60px;
  margin: 0 0 20px;
  font-size: 24px;
}
.img {
  margin-top: 280px;
  text-align: center;
}
</style>



